.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.leftContainer {
  position: absolute;
  width: calc(100% - 350px);
  max-width: 810px;
  left: 0;
}
.rightContainer {
  width: 300px;
  position: absolute;
  right: 0;
}
.barchart {
  display: inline-block;
}
.barchartEmbed {
  display: flex;
  max-width: 300px;
  float: right;
}

.mapEmbed {
  max-width: 500px;
  margin: 0 auto;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
